export class FileDependViewModel {
  /// <summary>
///  轉檔序號
/// <summary>
public exec_file_seq: number;
/// <summary>
///  轉檔相依序號
/// <summary>
public exec_file_depend_seq: number;
/// <summary>
///  相依檔名條件
/// <summary>
public depend_filename: string;
/// <summary>
///  建立者
/// <summary>
public creator: string;
/// <summary>
///  建立時間
/// <summary>
public create_time: Date;
/// <summary>
///  最後更新者
/// <summary>
public modifier: string;
/// <summary>
///  最後更新時間
/// <summary>
public last_update_time: Date;

/// <summary>
///  相依檔之檔案關鍵字
/// <summary>
public depend_file_keyword: string;

}



