import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import { String } from "../../Model/String";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { FileDependViewModel } from "../../Model/ViewModel/FileDependViewModel";

/**
 * Generated class for the FileDependModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-file-depend-add-edit-modal",
  templateUrl: "file-depend-add-edit-modal.html"
})
export class FileDependAddEditModalPage {
  item: FileDependViewModel;
  mode: string;
  CanEditBatch: boolean;
  title: string;
  exec_group: string;
  change_mode: string;
  file_keyword: string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    public modalCtrl: ModalController
  ) {
    this.mode = navParams.data.mode;
    this.change_mode = navParams.data.change_mode;
     this.item = new FileDependViewModel();
    this.item.exec_file_seq = navParams.data.item.exec_file_seq;
    this.item.exec_file_depend_seq = navParams.data.item.exec_file_depend_seq;
    this.item.depend_filename = navParams.data.item.depend_filename;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.item.depend_file_keyword = navParams.data.item.depend_file_keyword;

    this.CanEditBatch = navParams.data.CanEditBatch;

    this.title = String.Format("{0}", this.item.exec_file_seq);
    this.file_keyword = navParams.data.file_keyword;
    this.exec_group = navParams.data.exec_group;
  }
  SelectDepend() {
    let modal = this.modalCtrl.create("FileImportSelectModalPage", {
      exec_file_seq: this.item.exec_file_seq,
      exec_group:this.exec_group
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.exec_file_depend_seq = select_data.exec_file_seq;
      this.item.depend_file_keyword = select_data.file_keyword;
    });

    modal.present();
  }



  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad FileDependModalPage");
  }
}



